    sa_info: ['The Republic of South Africa, referred to as South Africa. Located in the southern hemisphere, it has the reputation of "the country of the rainbow", located at the southernmost tip of the African continent, with a land area of 1,219,090 square kilometers. South Africa is located in the southernmost part of the African continent. The latitude ranges from 22 to 35 in the south latitude and 17 to 33 in the longitude. It borders Namibia, Botswana, Zimbabwe, Mozambique and Swaziland in the north, and Lesotho in the middle, making it the largest country in China. East, South, The West is surrounded by the Indian Ocean and the Atlantic Ocean. The Cape of Good Hope route on the southwestern tip has always been one of the busiest sea lanes in the world, and is known as the Western Lifeline of the Sea.', 'South Africa is Africa largest economy and one of the BRICS countries. The nationals have a higher standard of living and the economy is relatively stable compared to other African countries. South Africa financial, legal, communications, energy, and transportation industries are relatively developed. With complete hardware infrastructure and stock trading market, gold and diamond production rank first in the world. Technology such as deep well mining is the world leader. In international affairs, South Africa has been identified as a medium power and maintains significant regional influence. ', 'South Africa, known as the "country of the rainbow", has a beautiful natural scenery and a wide variety of flora and fauna. Traveling around the world is the best portrayal of South Africa. South Africa has rich and diverse cultural characteristics, wildlife, and integrity. Urban urban planning, first-class road construction, fresh air, warm and hospitable people, and more investment opportunities than European and American countries have attracted thousands of tourists and entrepreneurs to explore.', 'The beauty of South Africa is special. It has both the boldness of Africa and the elegance of Europe. There is an intimate contact with the animals between the original and the modern. And its colorful tones, If the rainbow is so beautiful and beautiful... ']
